






































































































import { Vue } from 'vue-property-decorator';
import NguoiDungApi, { NguoiDungApiSearchParams } from '@/apiResources/NguoiDungApi';
import { NguoiDung } from '@/models/NguoiDung';

export default Vue.extend({
    $_veeValidate: {
        validator: 'new'
    },
    components: {},
    data() {
        return {
            isUpdate: false,
            nguoiDung: {} as NguoiDung,
            valid: false,
            isShow: false,
            loading: false,
            loadingSave: false,
            nguoiDungID: 0,
            searchParamsNguoiDung: {} as NguoiDungApiSearchParams,
        }
    },
    watch: {
    },
    methods: {
        hide() {
            this.isShow = false
        },
        show(isUpdate: boolean, item: any) {
            this.isShow = true;
            this.$validator.reset();
            this.loadingSave = false;
            this.isUpdate = isUpdate;
            if (isUpdate) {
                this.nguoiDungID = item.NguoiDungID;
                this.getDataFromApi(this.nguoiDungID);
            }
            else {
                this.nguoiDung = {} as NguoiDung;
            }
        },
        getDataFromApi(id: number): void {
            NguoiDungApi.detail(id).then(res => {
                this.nguoiDung = res;
            });
        },
        save(): void {
            this.$validator.validateAll('formNguoiDung').then((res) => {
               if (res) {
                    if (this.isUpdate) {
                        this.loading = true;
                        this.loadingSave = true;
                        NguoiDungApi.update(this.nguoiDungID, this.nguoiDung).then(res => {
                            this.loading = false;
                            this.isShow = false;
                            this.loadingSave = false;
                            this.$emit("save");
                            this.$snotify.success('Cập nhật thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.loadingSave = true;
                            this.$snotify.error('Cập nhật thất bại!');
                        });
                    } else {
                        this.loading = true;
                        this.loadingSave = true;
                        NguoiDungApi.insert(this.nguoiDung).then(res => {
                            this.nguoiDung = res;
                            this.isUpdate = true;
                           this.loadingSave = false;
                            this.loading = false;
                            this.isShow = false;
                            this.$emit("save");
                            this.$snotify.success('Thêm mới thành công!');
                        }).catch(res => {
                            this.loading = false;
                               this.loadingSave = true;
                            this.$snotify.error('Thêm mới thất bại!');
                        });
                    }
                }
            });
        }
    }
});
